/*
 *  Callout
 *    Styles for the callout section.
 *
 *  Version:
 *    1.0.0 - 2021/10/15
 */

.c-callout {
  padding: 5rem 0 4.0625rem;
  background-color: $s-color--background-subdued;

  &__top {
    padding: 0 $s-gutter--edge-mobile;
    text-align: center;
  }

  // Pre title
  &__pretitle {
    margin-bottom: 0.625rem;
  }

  // Title
  &__title {
    margin-bottom: 1.3125rem;
  }

  // Stars
  &__stars-container {
    display: flex;
    justify-content: center;
    margin-bottom: 0.625rem;
  }

  &__star {
    width: 1rem;
    height: 1rem;
    margin-right: 0.3125rem;

    &:last-child {
      margin-right: 0;
    }
  }

  // Content
  &__content {
    margin-bottom: 1.25rem;
  }

  // CTA
  &__cta-container {
    margin-bottom: 1.3125rem;
    text-align: center;
  }

  // Illustration
  &__illustration {
    display: block;
    width: 100%;
    max-width: 37.5rem;
    height: auto;
    margin: 0 auto;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    // Content
    &__content {
      width: 40%;
      max-width: 37.5rem;
      margin: 0 auto 1.25rem;
    }
  }
  @include t-media-query($s-medium-up) {
    padding: 7.6875rem 0 7.3125rem;

    &__pretitle {
      @include t-font--paragraph-1-laptop;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 12.8125rem 0 12.1875rem;

    &__pretitle {
      @include t-font--paragraph-1-desktop;
      margin-bottom: 1rem;
    }

    &__title {
      margin-bottom: 1.6875rem;
    }

    &__stars-container {
      margin-bottom: 0.75rem;
    }

    &__star {
      width: 1.125rem;

      margin-right: 0.375rem;
    }

    &_content {
      margin-bottom: 1.4375rem;
    }

    &__illustration {
      max-width: 53.75rem;
    }

    &__bottom {
      display: flex;
      flex-direction: column-reverse;
    }

    &__cta-container {
      margin-bottom: 0;
    }

    &__illustration-container {
      margin-bottom: 3.125rem;
    }
  }
}
